const mapDispatchToProps = {
    exportReport: exportActions.exportReport,
};
const ExportSubmitConnect = connect(mapStateToProps, mapDispatchToProps)(ExportSubmitBase);
const ExportSubmit = injectIntl(ExportSubmitConnect);
export { ExportSubmit };
