import { getQueryRoute } from 'api/queries/query';
import { addFilterToQuery, removeFilterFromQuery } from './filter';
export const getRouteForQuery = (history, query, reset = false) => {
    // Reset pagination
    if (reset) {
        query.filter = Object.assign(Object.assign({}, query.filter), { offset: 0 });
    }
    return `${history.location.pathname}?${getQueryRoute(query)}`;
};
export const handleCurrencySelected = (history, query, value, reset = false) => {
    const newQuery = Object.assign({}, JSON.parse(JSON.stringify(query)));
    history.replace(getRouteForQuery(history, newQuery, reset));
};
export const handleCostTypeSelected = (history, query, value, reset = false) => {
    const newQuery = Object.assign({}, JSON.parse(JSON.stringify(query)));
    history.replace(getRouteForQuery(history, newQuery, reset)); // Don't reset pagination
};
export const handleFilterAdded = (history, query, filter) => {
    const filteredQuery = addFilterToQuery(query, filter);
    history.replace(getRouteForQuery(history, filteredQuery, true));
};
export const handleFilterRemoved = (history, query, filter) => {
    const filteredQuery = removeFilterFromQuery(query, filter);
    history.replace(getRouteForQuery(history, filteredQuery, true));
};
export const handlePerPageSelect = (history, query, perPage) => {
    const newQuery = Object.assign({}, JSON.parse(JSON.stringify(query)));
    newQuery.filter = Object.assign(Object.assign({}, query.filter), { limit: perPage });
    const filteredQuery = getRouteForQuery(history, newQuery, true);
    history.replace(filteredQuery);
};
export const handleSetPage = (history, query, report, pageNumber) => {
    const limit = report && report.meta && report.meta.filter && report.meta.filter.limit ? report.meta.filter.limit : 10;
    const offset = pageNumber * limit - limit;
    const newQuery = Object.assign({}, JSON.parse(JSON.stringify(query)));
    newQuery.filter = Object.assign(Object.assign({}, query.filter), { offset });
    const filteredQuery = getRouteForQuery(history, newQuery);
    history.replace(filteredQuery);
};
export const handleSort = (history, query, sortType, isSortAscending, date = undefined) => {
    const newQuery = Object.assign({}, JSON.parse(JSON.stringify(query)));
    newQuery.order_by = {};
    newQuery.order_by[sortType] = isSortAscending ? 'asc' : 'desc';
    if (date) {
        newQuery.order_by.date = date;
    }
    const filteredQuery = getRouteForQuery(history, newQuery);
    history.replace(filteredQuery);
};
